<template>
    <div class="counter-component">
      <div class="counter-btn" @click="minus"> - </div>
      <div class="counter-show">
        <input type="text" v-model="number" @keyup="fixNumber">
      </div>
      <div class="counter-btn" @click="add"> + </div>
    </div>
</template>

<script>
export default {
  props: {
    max: {
      type: Number,
      default: 5
    },
    min: {
      type: Number,
      default: 1
    }
  },
  data () {
    return {
      number: this.min
    }
  },
  watch: {
    number () {
      this.$emit('on-change', this.number)
    }
  },
  methods: {
    fixNumber () {
      let fix
      if (typeof this.number === 'string') {
        fix = Number(this.number.replace(/\D/g, ''))
      }
      else {
        fix = this.number
      }
      if (fix > this.max || fix < this.min) {
        fix = this.min
      }
      this.number = fix
    },
    minus () {
      if (this.number <= this.min) {
        return
      }
      this.number --
    },
    add () {
      if (this.number >= this.max) {
        return
      }
      this.number ++
    }
  }
}
</script>

<style lang="less">
.counter-component{
  position: relative;
  display: inline-block;
  overflow: hidden;
  vertical-align: middle;
  .counter-show{
    float:left;
    input{
      border: none;
      border-top: 1px solid #9d9fb0;
      border-bottom: 1px solid #9d9fb0;
      height: 1.8rem;
      line-height: 1.8rem;
      width: 3.333rem;
      outline: none;
      text-align: center;
    }
  }
  .counter-btn{
    border: 1px solid #9d9fb0;
    float: left;
    height: 1.8rem;
    line-height: 1.8rem;
    width: 1.8rem;
    text-align: center;
    cursor: pointer;
    :hover{
      border-color: #4fc08d;
      background: #4fc08d;
      color: #fff;
    }
  }
}

</style>


