import Vue from 'vue'

import axios from 'axios'
var _bus = new Vue()  // 
var num = 0


//  
axios.interceptors.request.use((config) => { 
    num++
    _bus.$emit('showloading')
    return config

});

// 
axios.interceptors.response.use((res) =>{
 
    num--
    if (num <= 0) {
      _bus.$emit('closeLoading')      
    } else {
      _bus.$emit('showloading')      
    }

  if(res.status != 200){
    return Promise.reject(res);
    
  }
  
  return res;
});
export var bus = _bus   //


