<template>
  <div class="commodity-wrap">
    <nav-title title="" v-if="detailShow" :showPrev='showPrev' :prev="prev"></nav-title>
    <div class="topUp">
      <div class="topup_module">
        <template>
          <h3>:</h3>
            <Tabs type="card">
                <TabPane label=""></TabPane>
                <TabPane label=""></TabPane>
                <TabPane label=""></TabPane>
            </Tabs>
        </template>
      </div>
      <div class="phoneNumber">
        <h3>:</h3>
        <input type="tel" name="">
      </div>
      <div class="comm_Btn">
        <Button shape="circle"></Button>
      </div>
      <div class="goods_introduce"></div>    
    </div>
  </div>
</template>

<script>

import navTitle from '../../modules/title.vue'

export default{
    data () {
      return {

        detailShow: true, //  
        showPrev:true, // 
        prev: {path:'/malls'}, // name 
        title: '',


      }
    },
    created () {
        this.$store.state.showBottomNav = false // 
        this.singUUID = this.getDatas()  
    },
    mounted: function(){
      this.axios({
        method: 'post',
        data: {
          OPT: '234', 
          singUUID: this.singUUID || '',
        }
      }).then((response) =>{
            this.topUpData = response.data.correList
        }).catch((error) => {
            console.log(error)
        })
    },
    components: {navTitle},
}
</script>

<style lang="less">
.commodity-wrap{
   padding-top:3rem;
   .topUp{
    padding: 1rem;
   }
   
}

</style>



