import Vue from 'vue'
import Router from 'vue-router'
import Index from '../components/Index' //
import Lists from '../components/Lists' //
import Malls from '../components/Malls' //
import Users from '../components/Users' // 
import Login from '../components/Login' // 
import Novice from '../components/views/activity/novice' // 
import Invest from '../components/views/tender/invest' // 
import InvestSuccess from '../components/views/tender/investSuccess' // 
import News from '../components/views/find/news' // 
import NewsView from '../components/views/find/view' // 
import TelephoneFare from '../components/views/integral/telephoneFare' //
import CommodityPage from '../components/views/integral/commodityPage' //

import md5 from 'blueimp-md5';
import qs from 'qs'


import axios from 'axios'
import VueAxios from 'vue-axios'




Vue.use(Router,VueAxios,axios)



// 
const router =  new Router({
  // mode: 'history',
   routes:[
        {
          path: '/',
          name: 'Index',
          component: Index 
        },
        {  
          path: '/lists',  
          name: 'Lists',  
          component: Lists 
        },
        {  
          path: '/malls',  
          name: 'Malls',  
          component: Malls,
          meta: {
            requireAuth: false
          }
        },
        {  
          path: '/users',  
          name: 'Users',  
          component: Users
        },
        {  
          path: '/login',  
          name: 'Login',  
          component: Login   
        },
        {
          path: '/tender/invest',
          name: 'Invest',
          component: Invest
        },
        {
          path: '/activity/novice',
          name: 'novice',
          component: Novice
        },
        {
          path: '/find/news',
          name: 'news',
          component: News
        },
        {
          path: '/find/view',
          name: 'newsView',
          component: NewsView
        },
        {
          path: '/tender/investSuccess',
          name: 'investSuccess',
          component: InvestSuccess
        },
        {  
          path: '/integral/telephoneFare',  
          name: 'TelephoneFare',  
          component: TelephoneFare
        },
        {  
          path: '/integral/commodityPage',  
          name: 'CommodityPage',  
          component: CommodityPage
        }
      ]
});


// // 

router.beforeEach((to, from, next) => {
    if (to.meta.requireAuth) {
        if (localStorage.getItem('token')) {
            next()
        } else {
          next({
            path:'login',
            query : {redirect : to.fullPath}
          })
        }
    } else {
        next()
    }
})

export default router




// 
Date.prototype.Format = function (fmt) { 
    var o = {
        "M+": this.getMonth() + 1, // 
        "d+": this.getDate(), // 
        "h+": this.getHours(), // 
        "m+": this.getMinutes(), // 
        "s+": this.getSeconds(), // 
        "q+": Math.floor((this.getMonth() + 3) / 3), // 
        "S": this.getMilliseconds() // 
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
}

const curDate = new Date;
const localTime = curDate .getTime();
const offsetTime = curDate .getTimezoneOffset() *60000;
const utcTime = localTime +offsetTime;
const bjDate = new Date(utcTime + 3600000 * 8);
const bdtimesss = new Date(bjDate).Format('yyyy-MM-dd hh:mm:ss');


export const getTime = () => {
  return bdtimesss;

}
	


var SECRET = "GDgLwwdK270Qj1w4";

export const sign = (param) => {
    let secret = SECRET;
    let source;
    let signStr;
    //   
    let array = new Array();
    for (var key in param) {
      array.push(key);
    }
    array.sort();

    // -  
    let paramArray = [];
    for (var index in array) {
      let key = array[index];
      paramArray.push(key + "=" + param[key]);
    }
     
    source = paramArray.join("&") + secret;
    signStr = md5(source);
    return signStr;
  
}


export const getSignParam = (param) => {
  param._t = getTime();
  var s = sign(param);
  param._s = s;
  return param;

}


// url
axios.defaults.baseURL = BASE_URL


//  
axios.interceptors.request.use((config) => {

	if(config.method  === 'post'){
		let data = qs.parse(config.data)
		config.data = qs.stringify({
			...getSignParam(data)
		})
	}else if(config.method === 'get') {
        config.params = {
			     _t: _t,
            ...config.params
        }
    }
	return config;
},(error) =>{
	return Promise.reject(error);
});

// 
axios.interceptors.response.use((res) =>{

  console.log(res)

	if(res.status != 200){
		return Promise.reject(res);
    
	}else{
    if(res.data.error == '-1000'){//token
        localStorage.clear()
        // router.push('/login');
        var dfd = router.currentRoute.name
        console.log("-1000000",dfd)
        
          router.replace({
              path: 'login',
              query: {redirect: router.currentRoute.fullPath}
          })
        
    }else{
        return res
    }

  }
	return res;
}, (error) => {
  //404
	return Promise.reject(error);
});











