<script>
  import navTitle from '../../modules/title.vue'
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  export default {
    data () {
      return {
        detailShow: true,
        showPrev: true,
        prev: {path:'/lists'},
        bubble:false, // 
        singUUID: '', // id
        borrowId:'', // id
        investData:{}, // 
        volumes:{}, // 
        single:true,  //
        amount:0, //  
        showInterPicker: false, // 
        showBackPicker: false, // 
        interestValue: "", // text
        interestId: " ", // id
        interestLength: 0, // 
        backValue: "", // text
        backId: " ", // id
        backLength: 0, // 
        interestPickData: {  // 
          columns: 1, // picker
          default: [
            {
              text:'',
              value: " "
            }
          ], // 
          // 
          pData1: [
            {
              text:'',
              value: " "
            }
          ]
        },
        backPickData: { // 
          columns: 1, // picker
          default: [
            {
              text:'',
              value: " "
            }
          ], // 
          // 
          pData1: [
            {
              text:'',
              value: " "
            }
          ]
        },
        qqProfit:0, // 
        spProfit:0, // 
        savingsProfit:0,  // 
        mouth:1, // 
        userData:{}, //
        modal:false, // 
        investmodal:false, // 
        rechageModal:false, // 
        typeSolution:"", //
        recommendMoney:"",// 
        recommendProduct:"", // 
        productPeriod:"", // 
        cpnum:"", // 
        swiperOption: {
          // NotNextTick is a component's own property, and if notNextTick is set to true, the component will not instantiate the swiper through NextTick, which means you can get the swiper object the first time (if you need to use the get swiper object to do what Things, then this property must be true)
          // notNextTicknotNextTicktrueNextTickswiperswiperswipertrue
          notNextTick: true,
          // swiper configs swiperapi
          autoplay: false,
          direction : 'horizontal',
					loop: false,
					speed: 1000,
					autoplay: false,
					freeMode: false,
					slidesPerView: 6,
					touchRatio: 0.5,
					initialSlide:1,
					followFinger: false,
					centeredSlides: true,
          grabCursor : true,
          setWrapperSize :true,
          autoHeight: true,
          paginationClickable :true,
          mousewheelControl : true,
          observeParents:true,
          // if you need use plugins in the swiper, you can config in here like this
          // debugger
          debugger: true,
          // swiper callbacks
          // swiperswiper
           on: {
						slideChangeTransitionEnd: function(){
              document.getElementById('mouth').value = this.activeIndex
              document.getElementById('mouthText').innerHTML = this.activeIndex+''
              calcDelta4(this.activeIndex)
              function calcDelta4(mouth) {
                var investMoney = Number(document.getElementById('investMoney').value); //
                var interestVolumes = Number(document.getElementById('interestVolumes').value); //
                var vipPlusRate = Number(document.getElementById('vipPlusRate').value); //
                var investaprs = Number(document.getElementById('investaprs').value);//
                var addaprs = Number(document.getElementById('addaprs').value);//
                var mouth =Number(mouth); //
                var day = mouth * 30
                if(isNaN(interestVolumes)){
                  interestVolumes = 0
                }
                if(isNaN(vipPlusRate)){
                  vipPlusRate = 0
                }
                if (interestVolumes > 0) {
                    //
                    var pow = Math.pow((1 + ((investaprs / 100 + vipPlusRate / 100 + addaprs / 100 + interestVolumes / 100) / 365)), mouth * 30)
                    var pow2 = Math.pow((1 + ((investaprs / 100 + vipPlusRate / 100 + addaprs / 100 + interestVolumes / 100) / 365)), 365)
                    console.log(':=====', pow)
                    // pow = Math.pow((1 + ( (info.getApr()/100 + info.getVipPlusRate()/100  + interestVolumes.value/100 ) / 365)),scaleBar.getmPosition()* 30);
                  } else {
                    //
                    var pow = Math.pow((1 + ((investaprs / 100 + vipPlusRate / 100 + addaprs / 100) / 365)), mouth * 30)

                    var pow2 = Math.pow((1 + ((investaprs / 100 + vipPlusRate / 100 + addaprs / 100) / 365)), 365)

                    console.log(':=====', pow)
                    console.log('2:=====', pow2)
                  }
                  var target4 = (pow * investMoney - investMoney).toFixed(2)
                  var target5 = (pow2 * investMoney - investMoney).toFixed(2)
                  if (mouth < 12) {
                   document.getElementById("savingsProfit").innerHTML = target4
                  } else {
                    document.getElementById("savingsProfit").innerHTML = target5
                  }
