      investment () {
        let _this = this
        if(!this.valid()){
          return
        }
        this.axios({
            method: 'post',
            data: {
                bidId:_this.borrowId,
                signUUID: _this.singUUID || "",
                amount:_this.amount,
                OPT: 10042
              }
          }).then((response) => {
