      color:#fd5e5e;
    }
  }
  & .investContent{
    padding:1rem;
    background: #fff;
    font-size: 1rem;
    & .top{
      color: #8c96a7;
      line-height: 2rem;
      margin-bottom: 0.5rem;
      font-size:0.5rem;
      & .fl{
        & .red{
          color:#fd5e5e;
        }
      }
    }
    & .number{
      & .ivu-input-number{
        overflow: visible;
        width:50%;
        height:2.1rem;
        line-height: 2.1rem;
        border-radius: 0;
        box-sizing: border-box;
        border:0;
      }
      & .ivu-input-number-focused{
        box-shadow: none;
      }
      & .ivu-input-number-input-wrap{
        //  overflow: visible;
        // height:2.1rem;
        // line-height: 2.1rem;
        // border-radius: 0;
        // box-sizing: border-box;
        // border:0;
        position: absolute;
        z-index: 22;
        border:1px solid #dddee1;
        height:100%;
        width:100%;
      }
      & .ivu-input-number-handler-wrap{
        width:100%;
        opacity:1;
        background:transparent;
        box-sizing: border-box;
        border-left:0;
        & .ivu-input-number-handler-up{
          right:-2rem;
          width:2rem;
          height:100%;
          top:0;
          position: absolute;
          border:1px solid #dddee1;
          background: #d5d5d5;
        }
        & .ivu-input-number-handler-down{
          left:-2rem;
          width:2rem;
          height:100%;
          top:0;
          position: absolute;
          border:1px solid #dddee1;
          background: #d5d5d5;
        }
        & .ivu-input-number-handler-down-inner,& .ivu-input-number-handler-up-inner{
          width:100%;
          height:100%;
          right:0;
          line-height: 2rem;
          color:#fd5e5e;
        }
        & .ivu-input-number-handler-up-disabled .ivu-input-number-handler-up-inner,& .ivu-input-number-handler-down-disabled .ivu-input-number-handler-down-inner{
          color:#999!important;
        }
        & .ivu-icon-ios-arrow-up:before{
          content:'\F217'
        }
        & .ivu-icon-ios-arrow-down:before{
          content:'\F208'
        }
      }
    }
    & .saving_profit{
       color: #8c96a7;
      line-height: 2rem;
      font-size:0.5rem;
      text-align: left;
      & .red{
        color:#fd5e5e;
      }
    }
    & .invitation{
      padding:1rem 0;
      & a{
        color:#1e93ff;
      }
    }
    & .investBtn{
      width:100%;
      margin:0 0 1rem;
      & .ivu-btn{
        width:100%;
        background: -webkit-linear-gradient(left, #24e9f0 , #2564ef);
        background: -o-linear-gradient(right, #24e9f0 , #2564ef);
        background: -moz-linear-gradient(right, #24e9f0 , #2564ef);
        background: linear-gradient(to right, #24e9f0 , #2564ef);
        padding:0.5rem 0;
        border:0;
        color:#fff;
        font-size:1rem;
      }
      & .ivu-btn[disabled]{
        color: #f7f7f7;
        background: #cacaca;
        border-color: #dddee1;
      }
    }
    & .agreement{
      font-size: 0.6rem;
      text-align: left;
      & .ivu-checkbox-wrapper{
        margin-right:0
      }
      & a{
        color:#2c3e50;
      }
    }
    & .invest_line{
      text-align: center;
      border: 1px solid #ccc;
      margin-top: 1rem;
      background-color: #fff;
    }
    & .cytime{
      padding:1rem;
      color:#8c96a7;
      font-size: 0.8rem;
      & .relate{
        position: relative;
        & .rule_b{position:absolute;width:1px; height:2rem;background: #fd5555 ;top:1rem;left:50%;margin-left:-1px;}
        & .rule_b p{position:absolute;top:-1rem;left:-1rem;color:#fd5555;width:3rem;}
      }
    }
    & .swiper-container{
      height:4rem!important;
      & .swiper-slide{
        margin-top:2rem;
        position: relative;
        box-sizing: border-box;
        & p{
          position:absolute;
          top:-1rem;
          left:0.5rem;
        }
      }
      
      & img{
        max-width: 100%;
        border: 0;
      }
    }

  }
  & .ruleList{
    padding:1rem 0;
    & .rule{
      padding:1rem 0 0;
      font-size: 1rem;
      color:#1e93ff;
      & a{
        color:#1e93ff;
        padding:0 0.5rem;
      }
    }
  }
  & .investment_number{
    background: #fff;
    font-size: 1rem;
    padding: 1rem;
    margin-top: 1rem;
    & li{
      float: left;
      width:50%;
      color:#9ea2a5;
      text-align: center;
      & p{
        margin-top:0.5rem;
        & span{
          color:#fd5e5e;
          font-size: 1.3rem;
        }
      }
    }
  }
}

.vertical-center-modal{
    display: flex;
    align-items: center;
    justify-content: center;
    & .ivu-modal{
        top: 0;
      margin:40px!important;
        & .con-1{
          text-align: center;
          & .typeSolution{
            color:#1e93ff;
            font-size: 1.5rem;
          }
        }
        & .ivu-modal-footer{
          border-top:0;
          text-align: center;
        }
    }
}
</style>



